<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img
          src="http://coderblue.cn/images/ayer.png"
          width="46px"
          height="46px"
        />
        <span></span>
      </div>
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <!--  -->
    <el-container>
      <!--  isCollapse:truefalse-->
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!--  
        :unique-opened="true" collapse-transition: collapse:
        router:v-routerindexpath
        :default-active:-->
        <el-menu
          :default-active="$route.path"
          unique-opened
          :router="true"
          :collapse="isCollapse"
          :collapse-transition="false"
          background-color="#333744"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <!--  -->
          <el-submenu
            :index="item.id + '' + index"
            v-for="(item, index) in menuList"
            :key="item.id"
          >
            <!--  -->
            <template slot="title">
              <!--  -->
              <i :class="iconsObj[item.id]"></i>
              <!--  -->
              <span>{{ item.authName }}</span>
            </template>

            <!--  -->
            <el-menu-item
              :index="'/' + subItem.path"
              v-for="subItem in item.children"
              :key="subItem.id"
            >
              <i class="el-icon-menu"></i>
              <span>{{ subItem.authName }}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      // 
      menuList: [{ authName: '', id: 125, order: 1, path: 'users' }],
      // 
      iconsObj: {
        125: 'el-icon-user-solid',
        103: 'el-icon-lock',
        101: 'el-icon-shopping-bag-2',
        102: 'el-icon-shopping-cart-full',
        145: 'el-icon-tickets'
      },
      isCollapse: false
    }
  },
  created() {
    this.getMenuList()
  },
  methods: {
    logout() {
      // token
      window.sessionStorage.clear()
      // 
      this.$router.push('/login')
    },
    // async getMenuList() {
    //   const { data: res } = await this.$http.get('menus')
    //   if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
    //   this.menulist = res.data
    //   console.log(res)
    // }
    getMenuList() {
      this.$axios
        .get('/login?username=admin&&password=123456', {})
        .then(res => {
          this.menuList = [
            {
              authName: '',
              id: 125,
              order: 1,
              path: 'users',
              children: [
                {
                  authName: '',
                  id: 225,
                  order: 2,
                  path: 'users'
                }
              ]
            },
            {
              authName: '',
              id: 103,
              order: 1,
              path: 'rights',
              children: [
                {
                  authName: '',
                  id: 203,
                  order: 2,
                  path: 'roles'
                },
                {
                  authName: '',
                  id: 204,
                  order: 2,
                  path: 'rights'
                }
              ]
            },
            {
              authName: '',
              id: 101,
              order: 1,
              path: 'goods',
              children: [
                {
                  authName: '',
                  id: 200,
                  order: 2,
                  path: 'list'
                },
                {
                  authName: '',
                  id: 198,
                  order: 2,
                  path: 'categories'
                }
              ]
            },
            {
              authName: '',
              id: 102,
              order: 1,
              path: 'orders',
              children: [
                {
                  authName: '',
                  id: 202,
                  order: 2,
                  path: 'orders'
                }
              ]
            },
            {
              authName: '',
              id: 145,
              order: 1,
              path: 'reports',
              children: [
                {
                  authName: '',
                  id: 245,
                  order: 2,
                  path: 'reports'
                },
                {
                  authName: '',
                  id: 945,
                  order: 2,
                  path: 'map'
                }
              ]
            }
          ]
        })
    },
    // 
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    }
  }
}
</script>

<style lang="less" scoped>
  .home-container {
    height: 100%;
  }

  // 
  .el-header {
    background-color: #373d41;
    display: flex;
    justify-content: space-between;
    padding-left: 0;
    align-items: center;

    > div {
      display: flex;
      color: #fff;
      font-size: 20px;
      align-items: center;

      > span {
        margin-left: 15px;
      }
    }
  }

  .el-aside {
    background-color: #333744;

    .el-menu {
      // 
      border-right: none;
    }
  }

  .el-main {
    background-color: #eaedf1;
  }

  .toggle-button {
    background-color: #4a5064;
    font-size: 10px;
    line-height: 24px;
    color: #fff;
    text-align: center;
    // 
    letter-spacing: 0.2em;
    cursor: pointer;
  }
</style>
