<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoles"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-table :data="roleList" border stripe @expand-change="expandChange">
      <!--  -->
      <el-table-column type="expand">
        <template v-slot="scope">
          <el-row
            :class="['bdbottom', index === 0 ? 'bdtop' : '', 'vcenter']"
            v-for="(item, index) in scope.row.roles"
            :key="item.id + '' + index"
          >
            <!--  -->
            <el-col :span="5">
              <el-tag>{{ item.authName }}</el-tag>
              <i class="el-icon-caret-right"></i>
            </el-col>
            <!--  -->
            <el-col :span="19">
              <!--  -->
              <el-row
                :class="[index === 0 ? '' : 'bdtop', 'vcenter']"
                v-for="(item1, index) in item.children"
                :key="item1.id + '' + index"
              >
                <el-col :span="6">
                  <el-tag type="success">{{ item1.authName }}</el-tag>
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <!-- 24 -->
                <!--  -->
                <el-col :span="18">
                  <el-tag
                    type="warning"
                    v-for="(item2, index) in item1.children"
                    :key="item2.id + '' + index"
                  >
                    {{ item2.authName }}
                  </el-tag>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </template>
      </el-table-column>
      <el-table-column label="#" type="index"></el-table-column>
      <el-table-column label="" prop="roleName"></el-table-column>
      <el-table-column label="" prop="roleDesc"></el-table-column>
      <el-table-column label="" width="300">
        <template v-slot="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            ></el-button
          >
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            ></el-button
          >
          <el-tooltip
            effect="dark"
            content=""
            placement="top"
            :enterable="false"
          >
            <el-button style="padding: 0px;">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="showSetRightDialog(scope.row.id)"
                ></el-button
              >
            </el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title=""
      @close="handleClose"
      :visible.sync="setRightDialogVisible"
      width="50%"
    >
      <!--  -->
      <el-tree
        ref="treeRef"
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys="defaultKeys"
        @node-click="handleNodeClick"
      ></el-tree>
      <!-- :default-expanded-keys="defaultKeys" -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="updateRightDialogVisible"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addRolesDialogVisible"
      width="50%"
      @close="addRolesCloseDialog"
    >
      <el-form
        :model="addRolesRuleForm"
        status-icon
        :rules="addRolesRules"
        ref="addRolesRuleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="roleName">
          <el-input
            v-model="addRolesRuleForm.roleName"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input
            v-model="addRolesRuleForm.roleDesc"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRolesDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addNewRoles"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addRolesRuleForm: {
        roleName: '',
        roleDesc: ''
      },
      addRolesRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 15, message: ' 1  15 ', trigger: 'blur' }
        ]
      },
      roleList: [
        {
          authName: '',
          roleDesc: ''
        }
      ],
      setRightDialogVisible: false,
      // 
      rightsList: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // id
      defaultKeys: [504, 505, 506],
      // id
      rolesId: '',
      // 
      addRolesDialogVisible: false
    }
  },
  // 
  mounted() {
    // this.getPowerList()
  },
  created() {
    // this.getRoleList()
    this.getPowerList()
  },
  methods: {
    addRoles() {
      this.addRolesDialogVisible = true
    },
    getPowerList() {
      this.$axios.get('http://localhost:8081/roles/list').then(res => {
        if (res.data.code === 200) {
          this.roleList = res.data.data
          // 
          this.rightsList = res.data.data[0].roles
        }
      })
    },
    // getRoleList() {
    //   this.$axios.get('http://localhost:8081/roles/findAll').then(res => {
    //     // TO_DO
    //     if (res.data.code === 200) {
    //       this.roleList = res.data.data
    //     }
    //   })
    // },
    // 
    showSetRightDialog(id) {
      this.rolesId = id
      this.defaultKeys = []
      this.getPowerTree(id)
      // 
      setTimeout(() => {
        this.setRightDialogVisible = true
        // this.dialogVisible = truedom
        // dom
        // this.$refs.tree is undefinedsetCheckedKeysundefined
        // : this.$nextTick()this.$nextTick()dom
        this.$nextTick(function() {
          this.$refs.treeRef.setCheckedKeys(this.defaultKeys)
        })
      }, 300)
    },
    // 
    getPowerTree(rolesId) {
      this.$axios.get(`http://localhost:8081/roles/${rolesId}`).then(res => {
        if (res.data.code === 200) {
          if (res.data.data[0].powers !== null) {
            res.data.data[0].powers.forEach(item => {
              // 
              if (this.defaultKeys.indexOf(item.id) !== 0) {
                if (item.haveChildren) {
                  this.defaultKeys.push(item.id)
                }
              }
            })
          }
        }
      })
    },
    // 
    updateRightDialogVisible() {
      console.log(this.$refs.treeRef.getCheckedKeys())
      const keys = [
        // es6
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      // NullPointerException
      const idStr = keys.join(',')
      const params = this.qs.stringify({
        ids: idStr
      })
      this.$axios
        .post(`http://localhost:8081/roles/updateTree/${this.rolesId}`, params)
        .then(res => {
          if (res.data.code === 200) {
            this.setRightDialogVisible = false
            this.$message.success('')
          }
        })
        .catch(err => {
          this.$message.error('' + err)
        })
    },
    handleNodeClick(data, node) {
      // this.$refs.treeRef.setCheckedKeys(this.defaultKeys)
      console.log(this.$refs.treeRef.getHalfCheckedKeys())
      console.log(node.key)
      console.log(this.defaultKeys)
    },
    // 
    handleClose() {
      this.defaultKeys = []
    },
    expandChange(expandedRows) {
      var params = new URLSearchParams()
      params.append('id', expandedRows.id)
      this.$axios
        .put('http://localhost:8081/roles/expandTree', params)
        .then(res => {
          if (res.data.code === 200) {
          }
        })
    },
    // 
    addNewRoles() {
      var params = new URLSearchParams()
      params.append('roleName', this.addRolesRuleForm.roleName)
      params.append('roleDesc', this.addRolesRuleForm.roleDesc)
      console.log(this.addRolesRuleForm.roleDesc)
      this.$axios.post('http://localhost:8081/roles/add', params).then(res => {
        if (res.data.code === 200) {
          this.$message.success('')
          this.getPowerList()
          this.addRolesDialogVisible = false
        }
      })
    },
    // 
    addRolesCloseDialog() {
      this.$refs.addRolesRuleForm.resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}

// 
.vcenter {
  display: flex;
  align-items: center;
}
</style>
