<template>
  <div id="main" style="width: 600px;height:400px;"></div>
</template>

<script>
// 1.Echarts
import echarts from 'echarts'

export default {
  data() {
    return {}
  },
  mounted() {
    this.init()
  },
  created() {},
  methods: {
    init() {
      // domecharts
      var myChart = echarts.init(document.getElementById('main'))
      // 
      var option = {
        title: {
          text: 'ECharts '
        },
        tooltip: {},
        legend: {
          data: ['']
        },
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [
          {
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }
        ]
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="less" scoped></style>
