<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCatDialog"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        class="treeTable"
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
        <!-- slot="isOk" slot-scope="scope" -->
        <template v-slot:isOk="scope">
          <i
            class="el-icon-success"
            style="color: lightgreen;"
            v-if="scope.row.catDeleted === 0"
          ></i>
          <i class="el-icon-error" style="color: red;" v-else></i>
        </template>
        <!--  -->
        <template v-slot:sort="scope">
          <el-tag size="mini" v-if="scope.row.catLevel === 0"></el-tag>
          <el-tag
            size="mini"
            v-else-if="scope.row.catLevel === 1"
            type="success"
            ></el-tag
          >
          <el-tag size="mini" v-else type="warning"></el-tag>
        </template>
        <!--  -->
        <template v-slot:option="scope">
          <el-button
            type="primary"
            size="mini"
            @click="showEditDialog(scope.row)"
            ><i class="el-icon-edit"></i></el-button
          >
          <el-button type="warning" size="mini"
            ><i class="el-icon-delete"></i></el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      @close="closeDialog"
      :visible.sync="editDialogVisible"
      width="50%"
    >
      <el-form
        :model="editRuleForm"
        status-icon
        :rules="editRules"
        ref="editFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="catName">
          <el-input
            v-model="editRuleForm.catName"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="catDeleted">
          <el-input
            v-model="editRuleForm.catDeleted"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="updateGoodCat"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      @close="closeAddDialog"
      :visible.sync="addDialogVisible"
      width="50%"
    >
      <el-form
        :model="addRuleForm"
        status-icon
        :rules="addRules"
        ref="addFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="catName">
          <el-input v-model="addRuleForm.catName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pid">
          <el-cascader
            v-model="selectedCatKeys"
            :options="cascaderList"
            :props="cateProps"
            clearable
            filterable
            ref="cascader"
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addGoodCat"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    var valiCateDeleted = (rule, value, cb) => {
      if (value === 0 || value === '1' || value === '0') {
        cb()
      }
      cb(new Error('01'))
    }
    return {
      // 
      queryInfo: {
        type: 3,
        pageNum: 1,
        pageSize: 5
      },
      // 
      cateList: [
        {
          catName: '',
          catDelete: false,
          children: [
            { catName: '', children: [{ catName: '' }] }
          ]
        },
        {
          catName: '',
          catDelete: true,
          children: [{ catName: '' }]
        }
      ],
      // 
      total: 0,
      // table
      columns: [
        {
          label: '',
          prop: 'catName'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isOk'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'sort'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'option'
        }
      ],
      // 
      editDialogVisible: false,
      // 
      addDialogVisible: false,
      editRuleForm: {
        catName: '',
        catDeleted: 0,
        id: 0
      },
      editRules: {
        catName: [{ required: true, message: '' }],
        catDeleted: [{ validator: valiCateDeleted, trigger: 'blur' }]
      },
      addRuleForm: {
        catName: ''
      },
      addRules: {
        catName: [{ required: true, message: '' }]
      },
      // 
      cascaderList: [],
      // 
      cateProps: {
        label: 'catName',
        value: 'id',
        children: 'children',
        // 
        checkStrictly: true,
        // 
        expandTrigger: 'hover'
      },
      // 
      selectedCatKeys: [],
      // 
      selectedCat: {
        // :number
        catPid: '',
        catLevel: '',
        catDeleted: ''
      }
    }
  },
  created() {
    this.getCateList()
  },
  mounted() {
    this.getCascaderList()
  },
  methods: {
    getCateList() {
      this.$axios
        .get('http://localhost:8081/goods', {
          params: {
            pageNum: this.queryInfo.pageNum,
            pageSize: this.queryInfo.pageSize,
            catLevel: 0
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.cateList = res.data.data
            this.total = res.data.total
          }
        })
        .catch(err => {
          this.$message.error(':' + err)
        })
    },
    // 
    getCascaderList() {
      this.$axios
        .get('http://localhost:8081/goods/filterCat', {
          params: {
            catLevel: 0
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.cascaderList = res.data.goods
          }
        })
        .catch(err => {
          this.$message.error(':' + err)
        })
    },
    handleSizeChange(newPageSize) {
      this.queryInfo.pageSize = newPageSize
      this.getCateList()
    },
    handleCurrentChange(newPageNum) {
      this.queryInfo.pageNum = newPageNum
      this.getCateList()
    },
    showEditDialog(goods) {
      this.editRuleForm.id = goods.id
      this.editRuleForm.catName = goods.catName
      this.editRuleForm.catDeleted = goods.catDeleted
      this.editDialogVisible = true
    },
    // 
    // 
    closeDialog() {
      this.$refs.editFormRef.resetFields()
    },
    updateGoodCat() {
      this.$refs.editFormRef.validate(valid => {
        const params = this.qs.stringify({
          id: this.editRuleForm.id,
          catName: this.editRuleForm.catName,
          catDeleted: this.editRuleForm.catDeleted
        })
        this.$axios
          .put('http://localhost:8081/goods/update', params)
          .then(res => {
            if (res.data.code === 200) {
              this.getCateList()
              this.editDialogVisible = false
              this.$message.success('')
            }
          })
          .catch(err => {
            this.$message.error(':' + err)
          })
      })
    },
    // 
    showAddCatDialog() {
      this.addDialogVisible = true
    },
    // 
    handleChange() {
      if (this.selectedCatKeys.length === 1) {
        this.selectedCat.catPid = this.selectedCatKeys[0]
        this.selectedCat.catLevel = 1
      } else if (this.selectedCatKeys.length === 2) {
        this.selectedCat.catPid = this.selectedCatKeys[1]
        this.selectedCat.catLevel = 2
      }
      this.selectedCat.catDeleted = 0
    },
    // 
    addGoodCat() {
      this.$refs.editFormRef.validate(valid => {
        const params = this.qs.stringify({
          catPid: this.selectedCat.catPid,
          catLevel: this.selectedCat.catLevel,
          catName: this.editRuleForm.catName,
          catDeleted: this.selectedCat.catDeleted
        })
        this.$axios
          .post('http://localhost:8081/goods/addCat', params)
          .then(res => {
            if (res.data.code === 200) {
              this.getCateList()
              this.addDialogVisible = false
              this.$message.success('')
            }
          })
          .catch(err => {
            this.$message.error(':' + err)
          })
      })
    },
    // 
    closeAddDialog() {
      this.$refs.addFormRef.resetFields()
      // this.$refs.cascader.clearCheckedNodes is not a function"
      // 
      this.$refs.cascader.inputValue = ''
      // 
      this.$refs.cascader.checkedValue = ''
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}
</style>
