<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-table :data="rightsList" border stripe="">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="" prop="grade">
          <template v-slot="scope">
            <el-tag v-if="scope.row.grade === 0"></el-tag>
            <el-tag type="success" v-else-if="scope.row.grade === 1"
              ></el-tag
            >
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rightsList: [
        {
          id: 101,
          authName: '',
          grade: '0',
          pid: 0,
          path: 'goods'
        },
        {
          id: 102,
          authName: '',
          grade: '1',
          pid: 0,
          path: 'users'
        }
      ]
    }
  },
  created() {
    this.getRightsList()
  },
  methods: {
    // 
    getRightsList() {
      this.$axios
        .get('http://localhost:8081/powers/list')
        .then(res => {
          if (res.data.code === 200) {
            console.log(res.data)
            this.rightsList = res.data.data
            this.$message.success('')
          }
        })
        .catch(err => {
          this.$message.error(':' + err)
        })
    }
  }
}
</script>

<style lang="less" scoped></style>
