<template>
  <div id="map"></div>
</template>

<script>
// Echarts
import echarts from 'echarts'
// 
import 'echarts/map/js/china'
// jsonp
import jsonp from 'jsonp'

const option = {
  title: {
    text: ''
  },
  visualMap: [
    {
      // 
      type: 'piecewise',
      show: true,
      splitNumber: 6,
      pieces: [
        {
          min: 10000
        }, //  max max Infinity
        {
          min: 1000,
          max: 9999
        },
        {
          min: 100,
          max: 999
        },
        {
          min: 10,
          max: 99
        },
        {
          min: 1,
          max: 9
        },
        {
          min: 0,
          max: 0
        }
      ],
      inRange: {
        symbol: 'rect',
        color: ['#fff', '#ffaa85', '#ff7b69', '#cc2929', '#8c0d0d', '#660208']
      },
      itemHeight: 10,
      itemWidth: 20
    }
  ],
  // 
  //  : {a}{b}{c}, {d}
  tooltip: {
    trigger: 'item',
    formatter: '{b}<br/>{c}',
    position: function(point, params, dom, rect, size) {
      // 
      return [point[0], '10%']
    }
  },
  series: [
    {
      // 
      type: 'map',
      map: 'china',
      label: {
        // 
        show: true,
        fontSize: 12,
        color: 'black'
      },
      itemStyle: {
        // 
        areaColor: '#fff'
      },
      // 
      emphasis: {
        label: {
          fontSize: 15,
          color: '#4adbaf'
        },
        // 
        itemStyle: {
          //  
          areaColor: '#c7fffd',
          borderColor: '#B03A5B'
        }
      },
      // 
      zoom: 1.2,
      data: [
        { name: '', value: 200 },
        { name: '', value: 20000 }
      ]
    }
  ]
}

export default {
  data() {
    return {
      myChart: ''
    }
  },
  mounted() {
    // domecharts
    this.myChart = echarts.init(document.getElementById('map'))
    // 
    // 
    this.myChart.setOption(option)
  },
  created() {
    this.getData()
  },
  methods: {
    // 
    getData() {
      // 
      jsonp(
        'http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427',
        {},
        (erros, data) => {
          // console.log(data);
          // console.log(data.data.list)
          var lists = data.data.list.map(item => {
            return {
              name: item.name,
              value: item.value
            }
          })
          option.series[0].data = lists
          this.myChart.setOption(option)
        }
      )
    }
  }
}
</script>

<style lang="less" scoped>
#map {
  width: 1050px;
  height: 620px;
}
</style>
