/* eslint-disable no-useless-return */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!-- :Layout -->
      <el-row :gutter="20">
        <el-col :span="8">
          <!-- clearable -->
          <el-input
            clearable
            placeholder=""
            v-model="queryInfo.name"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="showAddDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="telephone"></el-table-column>
        <el-table-column label="" prop="role">
          <template v-slot="scope">
            <div v-if="scope.row.roles !== null">
              {{ scope.row.roles.roleName }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" prop="status">
          <template v-slot="scope">
            <div>
              <!-- {{ scope.row }}:json -->
              <el-switch
                @change="changeStatus(scope.row)"
                inactive-value="0"
                active-value="1"
                v-model="scope.row.status"
              ></el-switch>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <!-- slot-scope="scope"scope.row -->
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="showDeleteDialog(scope.row.id)"
            ></el-button>
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button style="padding: 0px;">
                <el-button
                  type="warning"
                  icon="el-icon-setting"
                  size="mini"
                  @click="showSetRolesDialog(scope.row)"
                ></el-button>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  id -->
    <el-dialog
      :title="title"
      @close="addDialogClosed"
      :visible.sync="editDialogVisible"
      width="50%"
    >
      <!--  -->
      <el-form
        :model="addUserForm"
        :rules="addUserRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addUserForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addUserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="addUserForm.telephone"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRolesdialogVisible"
      width="50%"
    >
      <div>
        <p>{{ userInfo.name }}</p>
        <p v-if="userInfo.roles != null">
          {{ userInfo.roles.roleName }}
        </p>
        <p v-else></p>
        <p>
          
          <el-select v-model="selectedRoleId" placeholder="">
            <el-option
              v-for="item in setRoles"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRolesdialogVisible = false"> </el-button>
        <el-button type="primary" @click="updateSetRoles(userInfo.id)"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^\w+((\.\w+){0,3})@\w+(\.\w{2,3}){1,3}$/
      if (regEmail.test(value)) {
        // 
        return cb()
      }
      cb(new Error(''))
    }

    // 
    var checkTelePhone = (rule, value, cb) => {
      // 
      const regTelePhone = /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/
      if (regTelePhone.test(value)) {
        // 
        return cb()
      }
      cb(new Error(''))
    }

    return {
      queryInfo: {
        name: '',
        // 
        pageNum: 1,
        // 
        pageSize: 2
      },
      userList: [],
      total: 0,
      // 
      isAddDialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      setRolesdialogVisible: false,
      // 
      setRoles: [],
      // id
      selectedRoleId: '',
      // title
      title: '',
      // 
      bool: false,
      // 
      addUserForm: {
        id: '',
        name: '',
        password: '',
        email: '',
        telephone: ''
      },
      // 
      userInfo: [],
      // 
      addUserRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 12,
            message: ' 6  12 ',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        telephone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkTelePhone, trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getUserList()
  },
  computed: {},
  methods: {
    search() {
      // 
      this.queryInfo.pageNum = 1
      this.getUserList()
    },
    getUserList() {
      console.log(this.queryInfo)
      this.$axios
        .get('http://localhost:8081/users/', {
          params: this.queryInfo
        })
        .then(res => {
          console.log(res.data)
          if (res.data.code === 200) {
            this.userList = res.data.data
            for (const item of this.userList) {
              this.statusToString(item)
              this.showRoleName(item)
            }
            this.total = res.data.total
            // 
            this.queryInfo.name = ''
          }
        })
    },
    // 
    handleCurrentChange(handleCurrentChange) {
      this.queryInfo.pageNum = handleCurrentChange
      // 
      this.getUserList()
    },
    // 
    handleSizeChange(handleSizeChange) {
      this.queryInfo.pageSize = handleSizeChange
      this.getUserList()
    },
    statusToString(item) {
      item.status = '' + item.status
    },
    showRoleName(item) {
      if (item.role === 0) {
        item.role = ''
      }
      if (item.role === 1) {
        item.role = ''
      }
      if (item.role === 2) {
        item.role = ''
      }
    },
    changeStatus(userInfo) {
      console.log(userInfo)
      this.$axios
        .put(
          `http://localhost:8081/users/changeStatus?id=${userInfo.id}&status=${userInfo.status}`
        )
        .then(res => {
          if (res.data.code === 200) {
            this.$message.success('')
            // 
            this.getUserList()
          }
        })
        .catch(err => {
          return this.$message.error(`:${err}`)
        })
    },
    // 
    addDialogClosed() {
      //  []
      // this.$refs.addFormRef.resetFields()
      this.resetForm()
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(valid => {
        if (valid) {
          if (this.addUserForm.id !== null) {
            this.changeUserFormData()
            this.$message.success('')
          } else {
            this.$message.success('')
          }
          // ie
          // var params = new URLSearchParams()
          // // params.append('name', this.addUserForm.name)
          // params.append('userInfo', this.addUserForm)
          // this.$axios
          //   .post('http://localhost:8081/users/add?', params)
          //   .then(response => (this.Datas = response.data.json.userMaps))
        }
      })
    },
    // 
    showAddDialog() {
      this.editDialogVisible = true
      this.title = ''
    },
    // 
    showEditDialog(userInfo) {
      this.editDialogVisible = true
      this.title = ''
      this.addUserForm.name = userInfo.name
      this.addUserForm.password = userInfo.password
      this.addUserForm.telephone = userInfo.telephone
      this.addUserForm.email = userInfo.email
      this.addUserForm.id = userInfo.id
    },
    // 
    resetForm() {
      this.addUserForm.name = ''
      this.addUserForm.password = ''
      this.addUserForm.telephone = ''
      this.addUserForm.email = ''
      this.addUserForm.id = ''
    },
    // 
    changeUserFormData() {
      const params = this.qs.stringify({
        id: this.addUserForm.id,
        name: this.addUserForm.name,
        email: this.addUserForm.email,
        telephone: this.addUserForm.telephone,
        password: this.addUserForm.password
      })
      this.$axios
        .post('http://localhost:8081/users/add', params)
        .then(success => {
          if (success.data.code === 200) {
            this.editDialogVisible = false
            this.bool = true
            this.getUserList()
          }
        })
        .catch(err => {
          this.bool = false
          this.$message.error(':' + err)
        })
    },
    // 
    showDeleteDialog(userId) {
      this.$confirm(`${userId}, ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$axios
            .delete('http://localhost:8081/users/delete', {
              params: {
                userId: userId
              }
            })
            .then(success => {
              if (success.data.code === 200) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.queryInfo.pageNum = 1
                this.getUserList()
              }
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    showSetRolesDialog(userInfo) {
      this.userInfo = userInfo
      this.$axios.get('http://localhost:8081/roles/list').then(res => {
        if (res.data.code === 200) {
          this.setRoles = res.data.data
          console.log(this.setRoles)
          // 
        }
      })
      this.setRolesdialogVisible = true
    },
    // 
    updateSetRoles(id) {
      var params = new URLSearchParams()
      params.append('role', this.selectedRoleId)
      params.append('id', id)
      this.$axios
        .put('http://localhost:8081/users/setRoles', params)
        .then(res => {
          if (res.data.code === 200) {
            this.$message.success('')
            this.setRolesdialogVisible = false
            this.getUserList()
          }
        })
    }
  }
}
</script>

<style lang="less" scoped></style>
